import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import loader from './25.gif'

class Form extends Component {
	constructor(props) {
		super(props);
		this.state = {
			username : '',
			email    : '',
			password : '',
			contact  : '',
			login    : false
		};
		this.handleChange = this.handleChange.bind(this);
		this.handleSignupSubmit = this.handleSignupSubmit.bind(this);
		this.handleLoginSubmit = this.handleLoginSubmit.bind(this);
	}

	handleChange(event) {
		this.setState({ [event.target.name]: event.target.value });
		// setstate of onchage of the value of the input
	}

	handleSignupSubmit(event) {
		event.preventDefault(); //this prevents the reloading of page on submit
		const content = {
			username : this.state.username,
			email    : this.state.email,
			password : this.state.password,
			contact  : this.state.contact
		};
		const requestOptions = {
			method  : 'POST',
			headers : { 'Content-Type': 'application/json' },
			body    : JSON.stringify(content)
		};
		// 'https://grocery1-inventory-app.herokuapp.com/auth/signup'
		fetch('https://grocery1-inventory-app.herokuapp.com/auth/signup', requestOptions)
			.then((response) => {
				if (!response.ok) throw new Error(`Something Went Wrong ${response.status}`);
				return response.json();
			})
			.then((data) => {
				console.log(data.message);
				console.log(data.token);
				localStorage.setItem(
					'login',
					JSON.stringify({
						login : true,
						token : data.token
					})
				);
				this.setState({ login: true });
			})
			.catch((err) => {
				alert(`Something Went Wrong:\n${err}\nCheck your Network Connection`);
				console.log(err);
			});
		this.setState({ username: '', email: '', password: '', contact: '' }); // clears the value in the input on submit
		let load = document.getElementById('load');
		load.style.display='block'
		load.style.marginBottom='40px'
	}
	handleLoginSubmit(event) {
		event.preventDefault(); //this prevents the reloading of page on submit
		const content = {
			username : this.state.username,
			password : this.state.password
		};
		const requestOptions = {
			method  : 'POST',
			headers : { 'Content-Type': 'application/json' },
			body    : JSON.stringify(content)
		};
		// 'https://grocery1-inventory-app.herokuapp.com/auth/signup'
		fetch('https://grocery1-inventory-app.herokuapp.com/auth/login', requestOptions)
			.then((response) => {
				console.log(response)
				if (!response.ok) throw new Error(`Something Went Wrong ${response.status}`);
				return response.json();
			})
			.then((data) => {
				console.log(data.message);
				console.log(data.token);
				localStorage.setItem(
					'login',
					JSON.stringify({
						login : true,
						token : data.token
					})
				);
				this.setState({ login: true });
			})
			.catch((err) => {
				alert(`Something Went Wrong:\n${err}\nYou Enter Invalid Username Or Password`);
				console.log(err);
			});
		this.setState({ username: '', password: '' }); // clears the value in the input on submit
		let load = document.getElementById('load');
		load.style.display='block'
		load.style.marginBottom='40px'
	}

	handelLogin() {
		let login = document.getElementById('Login');
		let signup = document.getElementById('Signup');
		let slide_button = document.getElementById('slide-btn');
		login.style.left = '15px';
		signup.style.left = '-480px';
		slide_button.style.left = '110px';
	}
	handelSignup() {
		let login = document.getElementById('Login');
		let signup = document.getElementById('Signup');
		let slide_button = document.getElementById('slide-btn');
		login.style.left = '-480px';
		signup.style.left = '15px';
		slide_button.style.left = '0px';
	}

	render() {
		return (
			<div>
				{this.state.login === true ? (
					<Redirect to="/home" />
				) : (
					<div className="form-box">
						<div className="button-box">
							<div id="slide-btn" className="btn" />
							<button type="button" className="toggle-btn" onClick={this.handelSignup.bind(this)}>
								Signup
							</button>
							<button type="button" className="toggle-btn" onClick={this.handelLogin.bind(this)}>
								Login
							</button>
							
						</div>
						<div id='load' style={{display:'none'}}>{(this.state.login === false) && <img src={loader} alt='loader'/> }</div>
						

						<form id="Signup" className="input-group form" onSubmit={this.handleSignupSubmit}>
							<input
								type="text"
								id="username"
								name="username"
								value={this.state.username}
								onChange={this.handleChange}
								className="input-field"
								placeholder="Username"
							/>
							<input
								type="text"
								id="email"
								name="email"
								value={this.state.email}
								onChange={this.handleChange}
								className="input-field"
								placeholder="E-mail"
							/>
							<input
								type="text"
								id="contact"
								name="contact"
								value={this.state.contact}
								onChange={this.handleChange}
								className="input-field"
								placeholder="Contact"
							/>
							<input
								type="text"
								id="password"
								name="password"
								value={this.state.password}
								onChange={this.handleChange}
								className="input-field"
								placeholder="Password"
							/>
							<br />
							<button className="submit-btn">Submit!</button>
						</form>
						<form id="Login" className="input-group form" onSubmit={this.handleLoginSubmit}>
							<input
								type="text"
								id="username"
								name="username"
								value={this.state.username}
								onChange={this.handleChange}
								className="input-field"
								placeholder="Username"
							/>
							<input
								type="text"
								id="password"
								name="password"
								value={this.state.password}
								onChange={this.handleChange}
								className="input-field"
								placeholder="Password"
							/>
							<br />
							<button className="submit-btn">Submit!</button>
						</form>
					</div>
				)}
			</div>
		);
	}
}

export default Form;
