import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import './form.css'

class Nav extends Component {
    handleClick(){
        localStorage.clear();
    }
	render() {
		return (
			<div className="nav">
				<h3>{`< GroceryHub />`}</h3>
                <Link className="link" to="/react-demo/" onClick={this.handleClick.bind(this)}><h3>Logout</h3></Link>
			</div>
		);
	}
}

export default Nav;
