import React, { Component } from 'react';

class Stock extends Component {
	render() {
		return (
				<div style={{width:'80%',textAlign:'left',fontSize:'18px',fontFamily:'Monospace',color:'slateblue',display:'flex', alignItems:'center',justifyItems:'space-between',border:'2px solid gray',padding:'20px',borderRadius:'10px'}}>
					<div>
						<h2>Available Categories</h2>
						<ul style={{ textAlign: 'left',width:'50%',fontFamily:'Monospace',fontSize:'18px',color:'gray' }}>
							{this.props.categories.map((c) => <li key={c._id}>{c.title}</li>)}
						</ul>
					</div>
					<h2 style={{color:'tomato'}}>{`>:)> ${this.props.categories.length} Categories in Stock`}</h2>
				</div>
		);
	}
}

export default Stock;
