import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import Nav from './Nav';
import Stock from './Stock';

class Home extends Component {
	constructor(props) {
		super(props);
		this.state = {
			categories : []
		};
	}

	componentDidMount() {
		console.log('Home Component Mounted');
		//fetch the Categories from the Grocery Api
		const store = JSON.parse(localStorage.getItem('login'));
		const requestOptions = {
			headers : { 'Content-Type': 'application/json', Authorization: `Bearer ${store.token}` }
		};
		fetch('https://grocery1-inventory-app.herokuapp.com/inventory/categories', requestOptions)
			.then((response) => {
				if (!response.ok) throw new Error(`Something Went Wrong: ${response.status}`);
				return response.json();
			})
			.then((data) => {
				console.log(data);
				this.setState({categories:data.Catogries})
			})
			.catch((err) => {
				console.log(err);
				
			});
	}

	render() {
		return (
			<div style={{width:'90%', margin:'auto'}}>
				<Nav />
				<div>
					{localStorage.getItem('login') !== null ? (
						<div className="home">
							<h1>
								Hello there and Welcome to the<br />GroceryHub
							</h1>
						</div>
					) : (
						<Redirect to="/react-demo/" />
					)}
				</div>
				<Stock categories={this.state.categories} />
			</div>
		);
	}
}

export default Home;
